<template>
  <v-card flat app>
    <v-toolbar dark color="primary" app>
      <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
      <v-btn
        small
        color="error"
        :to="{ name: 'Home' }"
      >
        <v-icon>cancel</v-icon>
        Отменить
      </v-btn>
      <v-btn
        small
        color="success"
        @click.native="checkForUnansweredQuestions"
      >
        <v-icon>check</v-icon>
        Завершить
      </v-btn>
    </v-toolbar>

    <v-layout row pb-2>
      <v-flex :class="{ 'xs8': $vuetify.breakpoint.mdAndUp, 'offset-xs2': $vuetify.breakpoint.mdAndUp }">
        <v-card>
          <v-card-title class="headline">{{ test.title }}</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <div class="mx-4 my-2" v-html="test.description"></div>
            <question-card></question-card>
            <div class="text-xs-center">
              <v-pagination
                :length="test.questions.length"
                v-model="current_question"
                circle
              ></v-pagination>
            </div>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-card>
</template>
<script>
import TesterMixin from './mixin'
import QuestionCard from './QuestionCard'

export default {
  mixins: [TesterMixin],

  components: {
    'question-card': QuestionCard
  },

  mounted () {
    this.$store.dispatch('tester/getTestByID', this.$route.params.id)
  },

  methods: {
    checkForUnansweredQuestions () {
      let firstUnanswered = this.$checkForUnansweredQuestions(this.test)
      if (firstUnanswered !== -1) {
        this.current_question = firstUnanswered + 1
        this.$store.commit('setSnackbar', { success: false, message: 'У Вас остались неотвеченные вопросы' })
        return false
      }

      this.$store.dispatch('tester/submitTestForCheck', this.test)
      this.$router.push({ name: 'Home' })
    }
  }
}
</script>
<style>
.title--shrinked {
  height: 53px;
  margin-top: 10px
}
</style>



