<template>
  <v-card flat app>
    <v-toolbar dark color="primary" app>
      <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
    </v-toolbar>

    <v-layout row pb-2>
      <v-flex :class="{ 'xs8': $vuetify.breakpoint.mdAndUp, 'offset-xs2': $vuetify.breakpoint.mdAndUp }">
        <v-card>
          <v-card-title class="headline">Доступные тесты</v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <v-data-table
              :items="getPublishedTests"
              :headers="headers"
              rows-per-page-text="Показывать на странице: "
              :rows-per-page-items="[10, 25, 50, { 'text': 'All', 'value': -1 }]"
            >
              <template slot="items" slot-scope="props">
                <td><router-link :to="{ name: 'Tester', params: { id: props.item._id } }">{{ props.item.title }}</router-link></td>
                <td>{{ props.item.author.username }}</td>
                <td>{{ props.item.questions.length }}</td>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
 </v-card>
</template>
<script>
import { TestMixin } from '@/components/mixins'

export default {
  mixins: [TestMixin],

  data () {
    return {
      headers: [
        { value: 'title', text: 'Название' },
        { value: 'author_username', text: 'Автор' },
        { value: 'questions_count', text: 'Вопросов' }
      ]
    }
  }
}
</script>



