import { mapGetters } from 'vuex'

var TestMixin = {
  computed: {
    ...mapGetters({
      'tests': 'test/tests',
      'getPublishedTests': 'test/publishedTests'
    })
  },

  mounted () {
    if (!this.tests.length) this.updateTests()
  },

  methods: {
    updateTests () {
      this.$store.dispatch('test/GET_TESTS')
    },

    deleteTest (item) {
      confirm('Вы уверены что хотите удалить тест') && this.$store.dispatch('test/DELETE_TEST', item)
    }
  }
}

export default TestMixin



