import { mapState } from 'vuex'

var TesterMixin = {
  computed: {
    current_question: {
      get () { return this.$store.state.tester.currentQuestion },
      set (value) { this.$store.commit('tester/SET_CURRENT_QUESTION', value) }
    },
    question: {
      get () { return this.test.questions[this.current_question - 1] }
    },
    ...mapState({
      test: state => state.tester.test
    })
  },

  methods: {
    $checkForUnansweredQuestions (test) {
      for (let [index, question] of test.questions.entries()) {
        if (question.answers.filter(answer => !!answer.correct).length === 0) return index
      }
      return -1
    }
  }
}

export default TesterMixin



