import { mapGetters } from 'vuex'

var QuestionMixin = {
  computed: {
    ...mapGetters({
      'questions': 'questions/questions'
    })
  },

  mounted () {
    if (!this.questions.length) this.updateQuestions()
  },

  methods: {
    updateQuestions () {
      this.$store.dispatch('questions/GET_ALL_QUESTIONS')
    },

    deleteQuestion (item) {
      confirm('Вы уверены что хотите удалить вопрос?') && this.$store.dispatch('questions/DELETE_QUESTION', item)
    }
  }
}

export default QuestionMixin



