<template>
  <v-card flat>
    <v-divider></v-divider>
    <v-card-title class="subheading noselect" v-html="question.question"></v-card-title>

    <v-card-text v-if="question.type === 'выбор'">
      <div
        v-for="(answer, index) in question.answers"
        :key="index"
        class="py-2 px-2 answer noselect"
        :class="answer.correct && 'primary white--text'"
        @click="$store.commit('tester/CHANGE_ANSWER_STATE', answer)"
      >
        <v-icon color="white" v-if="answer.correct">check</v-icon>
        {{index + 1}}) {{answer.answer}}
      </div>
    </v-card-text>
  </v-card>
</template>
<script>
import TesterMixin from './mixin'

export default {
  mixins: [TesterMixin]
}
</script>
<style>
.answer {
  border-radius: 15px;
  cursor: pointer;
}
</style>



