import { mapGetters } from 'vuex'

var QuestionPackMixin = {
  computed: {
    ...mapGetters({
      'question_packs': 'questionPack/questionPacks'
    })
  },

  mounted () {
    if (!this.question_packs.length) this.updateQuestionPacks()
  },

  methods: {
    updateQuestionPacks () {
      this.$store.dispatch('questionPack/GET_QUESTION_PACKS')
    },

    deleteQuestionPack (item) {
      confirm('Вы уверены что хотите удалить сборник вопросов?') && this.$store.dispatch('questionPack/DELETE_QUESTION_PACK', item)
    }
  }
}

export default QuestionPackMixin



